body .sliderPopup p #close:hover {
  cursor: pointer;
}
body .sliderPopup .popupSlider {
  margin: 50px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .sliderPopup .popupSlider li {
  width: 100%;
  text-align: center;
  user-select: none;
}
body .sliderPopup .popupSlider li img {
  width: 475px;
}
body .sliderPopup #prev,
body .sliderPopup #next {
  height: 570px;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .sliderPopup #prev i,
body .sliderPopup #next i {
  font-size: 5em;
  color: #009fff;
}
body .sliderPopup #prev i:hover,
body .sliderPopup #next i:hover {
  cursor: pointer;
}
body .sliderPopup .popupNav {
  display: flex;
  justify-content: center;
}
body .sliderPopup .popupNav li {
  margin: 14px;
  width: 65px;
  height: 65px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #c5c5c5;
}
body .sliderPopup .popupNav li img {
  padding: 5px;
  width: 100%;
}
body .sliderPopup .popupNav li:hover {
  cursor: pointer;
}
body .sliderPopup .popupNav .cycle-pager-active {
  border-color: #009fff;
}
