@import "components/header";
@import "components/aside";
@import "components/footer";

body{
  .content{
    margin: 25px 0 35px;
    .container{
      .row{
        .col-lg-3{
          .aside{
            &__bestHolo,
            &__newHolo,
            &__threeD,
            &__examples,
            &__custom{
              display: none;
            }
          }
        }
        .col-lg-4{
          .product{
            &__slider{
              .slider{
                li{
                  width: 345px;
                  height: 345px;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  border: 1px solid #c5c5c5;
                  img{
                    padding: 20px;
                    width: 100%;
                  }
                  &:hover{
                    cursor: pointer;
                  }
                }
              }
              .nav{
                display: flex;
                flex-wrap: wrap;
                li{
                  margin: 14px;
                  width: 65.3px;
                  height: 65.3px;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  border: 1px solid #c5c5c5;
                  &:nth-child(4n+1){
                    margin: 14px 14px 14px 0;
                  }
                  img{
                    padding: 5px;
                    width: 100%;
                  }
                  &:hover{
                    cursor: pointer;
                  }
                }
                .cycle-pager-active{
                  border-color: #009fff;
                }
              }
            }
            .customize{
              margin: 35px 0;
              width: 90%;
              display: block;
              font: 1em/35px "Arial";
              color: #010101;
              text-align: center;
              text-decoration: none;
              background-color: #cfcfcf;
            }
            .rating{
              ul{
                display: flex;
                align-items: center;
                li{
                  margin: 0 2.5px;
                  i{
                    color: #e7cb73;
                    line-height: 1em;
                    &:hover{
                      cursor: pointer;
                    }
                  }
                  &:nth-child(1){
                    margin: 0 2.5px 0 0;
                  }
                  a{
                    margin: 0 0 0 10px;
                    display: block;
                    font: .75em/1em "Arial";
                    color: #0081ef;
                  }
                }
              }
            }
          }
        }
        .col-lg-5{
          .product{
            &__nav{
              ul{
                display: flex;
                li{
                  a{
                    display: inline-block;
                    font: .875em/40px "Arial";
                    color: #0081ef;
                  }
                  .heart,
                  .exchange{
                    width: 50px;
                    color: #777;
                    text-align: center;
                    background-color: #eee;
                    border: 1px solid #dfdfdf;
                  }
                  &:nth-child(3){
                    margin: 0 0 0 35px;
                  }
                }
              }
            }
            &__headline{
              margin: 25px 0 35px;
              p{
                font: 1.25em/1.375em "Arial";
                font-weight: bold;
              }
            }
            &__options{
              .size{
                margin: 7px 0;
                display: flex;
                p{
                  font: .875em/1.5em "Arial";
                  font-weight: bold;
                  color: #525252;
                }
                ul{
                  margin: 0 0 0 15px;
                  li{
                    display: flex;
                    align-items: center;
                    span{
                      font: .875em/1.5em "Arial";
                    }
                  }
                }
              }
              .shape,
              .baseColor,
              .serialNumber{
                margin: 7px 0;
                display: flex;
                p{
                  font: .875em/1.5em "Arial";
                  font-weight: bold;
                  color: #525252;
                }
                ul{
                  display: flex;
                  li{
                    margin: 0 0 0 15px;
                    display: flex;
                    align-items: center;
                    span{
                      font: .875em/1.5em "Arial";
                    }
                  }
                }
              }
            }
            &__quantity{
              margin: 40px 0 70px;
              width: 370px;
              p{
                font: 1.25em/1.375em "Arial";
                font-weight: bold;
              }
              table{
                width: 100%;
                caption{
                  margin: 10px 0;
                  font: .75em/.875em "Arial";
                  text-align: left;
                }
                tr{
                  th{
                    font: .875em/35px "Arial";
                    font-weight: bold;
                    text-align: center;
                    background-color: #ececec;
                    border-top: 1px solid #b6b6b6;
                    border-bottom: 1px solid #b6b6b6;
                  }
                  td{
                    border-top: 1px solid #b6b6b6;
                    border-bottom: 1px solid #b6b6b6;
                    a{
                      width: 100%;
                      display: block;
                      font: .875em/35px "Arial";
                      font-weight: bold;
                      color: #0081ef;
                      text-align: center;
                    }
                    span{
                      width: 100%;
                      display: block;
                      font: .875em/35px "Arial";
                      text-align: center;
                    }
                  }
                }
              }
              .customQuantity{
                ul{
                  margin: 25px 0;
                  display: flex;
                  justify-content: space-between;
                  li{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                  }
                  .quantity{
                    width: 40%;
                    p{
                      font: 1em/35px "Arial";
                      font-weight: bold;
                    }
                    input{
                      width: 65px;
                      height: 35px;
                      font: 1em/35px "Arial";
                      color: #5c5c5c;
                      text-align: center;
                      border: 1px solid #c7c7c7;
                      outline: none;
                    }
                  }
                  .total{
                    width: 55%;
                    p{
                      width: 80px;
                      height: 35px;
                      font: 1em/35px "Arial";
                      color: #fff;
                      text-align: center;
                      background-color: #9e9e9e;
                    }
                    span{
                      font: 1.813em/35px "Arial";
                      font-weight: bold;
                    }
                  }
                }
                .orderInfo{
                  text-align: center;
                  span{
                    font: .875em/1em "Arial";
                    font-weight: bold;
                    &:nth-child(4){
                      font-size: 1.188em;
                    }
                  }
                }
                a{
                  margin: 20px 0;
                  width: 100%;
                  display: block;
                  font: 1.125em/35px "Arial";
                  font-weight: bold;
                  color: #fff;
                  text-align: center;
                  text-decoration: none;
                  background-color: #009fff;
                  &:hover{
                    background-color: #007eec;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .sliderPopup{
    padding: 45px;
    width: 1260px;
    height: 820px;
    display: none;
    position: fixed;
    margin: -410px 0 0 -630px;
    top: 50%;
    left: 50%;
    background: #fff;
    opacity: 0;
    z-index: 150;
    p{
      font: 1.375em/1.5em "Arial";
      font-weight: bold;
      text-align: center;
      position: relative;
      #close{
        position: absolute;
        top: -70px;
        right: -80px;
        color: #c0c0c0;
        &:hover{
          cursor: pointer;
        }
      }
    }
    .popupSlider{
      margin: 50px 0;
      display: flex;
      justify-content: center;
      align-items: center;
      li{
        width: 100%;
        text-align: center;
        user-select: none;
        img{
          width: 475px;
        }
      }
    }
    #prev,
    #next{
      height: 570px;
      display: flex;
      justify-content: center;
      align-items: center;
      i{
        font-size: 5em;
        color: #009fff;
        &:hover{
          cursor: pointer;
        }
      }
    }
    .popupNav{
      display: flex;
      justify-content: center;
      li{
        margin: 14px;
        width: 65px;
        height: 65px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid #c5c5c5;
        img{
          padding: 5px;
          width: 100%;
        }
        &:hover{
          cursor: pointer;
        }
      }
      .cycle-pager-active{
        border-color: #009fff;
      }
    }
  }
}